import Vue from "vue";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import axios from "axios";

export default Vue.extend({
  name: "Login",
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      form: {
        email: "",
        password: "",
        isValid: true,
        hasErrors: false,
        submitted: false,
        invalid: false
      }
    };
  },
  // computed: {
  //   //   ...mapState("account", ["status"])
  //   valid() {
  //     const isValid = this.$validator.validate();
  //     return isValid;
  //   }
  // },
  created() {
    // reset login status
    this.form.email = "";
    localStorage.removeItem("token");
    localStorage.removeItem("email");
  },
  methods: {
    //...mapActions("account", ["login", "logout"]),
    // handleSubmit(e) {
    //   this.submitted = true;
    //   const { username, password } = this.form;
    //   if (username && password) {
    //     //this.login({ username, password });
    //   }
    // },
    async onSubmit(valid: boolean) {
      this.form.isValid = valid;
      this.form.hasErrors = !valid;
      console.log(valid);
      //const isValid = false;
      //this.form.validated = true;
      //this.form.validated = await this.$validator.validate();
      //console.log(this.$refs.observer.validator().validate());
      console.log("Submitting");
      if (valid) {
        console.log("Basic validation passed. Submitting for token");

        this.form.submitted = true;
        axios
          .post("http://localhost:1337/auth/local", {
            identifier: this.form.email,
            password: this.form.password
          })
          .then(response => {
            const { data } = response;
            console.log(data);
            this.form.invalid = false;
            const {
              jwt,
              user: { email }
            } = data;
            localStorage.setItem("token", jwt);
            localStorage.setItem("email", email);

            const redirect = this.$route.query.redirect?.toString() || "Home";

            this.$router.replace({ name: redirect });
          })
          .catch(e => {
            console.log(e);
            this.form.invalid = true;
          })
          .finally(() => {
            this.form.submitted = true;
          });
      }
      // const isValid = await this.$refs.observer.validate();
      // if (!isValid) {
      //   console.log("is not valid");
      //   alert("Please fix errors first!");
      // } else {
      //   console.log("is valid");
      //   console.log(this.name + " signed in with password " + this.password);
      //   // reset fields
      //   this.name = "";
      //   this.password = "";
      //   // reset validation
      //   // You should call it on the next frame
      //   requestAnimationFrame(() => {
      //     this.$refs.observer.reset();
      //   });
      // }
    },
    login() {
      console.log("Log me in!");
    }
  }
});
